<template>
    <b-container >
        <b-row style="min-height:79.5vh" class="d-flex align-items-center">
            <b-col cols="12" sm="7">
                <img src="@/assets/pageNotFound.png" style="width:80%;height:auto">
            </b-col>
            <b-col cols="12" sm="5">
                <p class="font-weight-bold h1 system-color">Oops! Did you get lost?</p>
                <p class="h5">Sorry about that. The page you were looking for doesn't exist at the moment.</p>
                <p class="mt-4">You will be redirected to the Home Page in <span class="system-color">{{ secondsLeft }}</span> seconds.</p>
            </b-col>
        </b-row>
    </b-container>
</template>

<script>
// @ is an alias to /src
export default {
  name: 'PageNotFound',
  data(){
    return {
        secondsLeft: null,
        redirectInterval : null
    }
  },
  mounted(){
        this.secondsLeft = 5

        this.redirectInterval = setInterval(() => {
            if(this.secondsLeft > 0) this.secondsLeft -= 1
            if(this.secondsLeft == 0) this.$router.push({ path: '/' })
        }, 1000)
  },
  beforeDestroy(){
    clearInterval(this.redirectInterval)
  },    
  methods:{
      
  },
  computed:{
       
  },
  watch: {
      
  }
}
</script>

